import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import api from '../services/api';
import { toast } from 'react-toastify';
import DocumentsSection from '../components/DocumentsSection';
import './EmployeeDashboard.css';

const EmployeeDashboard = () => {
  const { user } = useAuth();
  const [generalDocuments, setGeneralDocuments] = useState([]);
  const [personalDocuments, setPersonalDocuments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('general');

  useEffect(() => {
    if (user) {
      loadDocuments();
    }
  }, [user]);

  const loadDocuments = async () => {
    try {
      setLoading(true);
      
      // Charger les documents gnraux
      const generalResponse = await api.get('/documents/general');
      if (generalResponse.data.success) {
        setGeneralDocuments(generalResponse.data.data);
      }
      
      // Charger les documents personnels si l'utilisateur a un ID employ
      if (user.employeeId) {
        const personalResponse = await api.get(`/documents/personal/${user.employeeId}`);
        if (personalResponse.data.success) {
          setPersonalDocuments(personalResponse.data.data);
        }
      }
      
    } catch (error) {
      console.error('Erreur lors du chargement des documents:', error);
      toast.error('Erreur lors du chargement des documents');
    } finally {
      setLoading(false);
    }
  };

  const handleDownload = async (documentId, documentTitle) => {
    try {
      console.log(' Tlchargement document:', documentId);
      
      // Construire l'URL de tlchargement avec l'ID employ si ncessaire
      let downloadUrl = `/documents/${documentId}/download`;
      if (user.employeeId) {
        downloadUrl += `?employeeId=${user.employeeId}`;
      }
      
      // Crer un lien de tlchargement temporaire
      const link = document.createElement('a');
      link.href = `${import.meta.env.VITE_API_URL || 'https://boulangerie-planning-api-4-pbfy.onrender.com/api'}${downloadUrl}`;
      link.download = documentTitle;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      
      toast.success(` Tlchargement de "${documentTitle}" dmarr`);
      
    } catch (error) {
      console.error('Erreur lors du tlchargement:', error);
      toast.error('Erreur lors du tlchargement du document');
    }
  };

  const getCategoryIcon = (category) => {
    const icons = {
      'notice': '',
      'procedure': '',
      'formation': '',
      'payslip': '',
      'contract': '',
      'other': ''
    };
    return icons[category] || '';
  };

  const getCategoryLabel = (category) => {
    const labels = {
      'notice': 'Notice',
      'procedure': 'Procdure',
      'formation': 'Formation',
      'payslip': 'Fiche de paie',
      'contract': 'Contrat',
      'other': 'Autre'
    };
    return labels[category] || category;
  };

  const formatFileSize = (bytes) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('fr-FR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    });
  };

  if (loading) {
    return (
      <div className="employee-dashboard">
        <div className="loading-container">
          <div className="loading"></div>
          <p>Chargement de vos documents...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="employee-dashboard">
      <div className="dashboard-header">
        <h1> Mes Documents</h1>
        <p>Bienvenue {user?.name || 'Employ'}, consultez vos documents personnels et gnraux</p>
      </div>

      <div className="dashboard-tabs">
        <button 
          className={`tab ${activeTab === 'general' ? 'active' : ''}`}
          onClick={() => setActiveTab('general')}
        >
           Documents Gnraux ({generalDocuments.length})
        </button>
        <button 
          className={`tab ${activeTab === 'personal' ? 'active' : ''}`}
          onClick={() => setActiveTab('personal')}
        >
           Mes Documents ({personalDocuments.length})
        </button>
      </div>

      <div className="dashboard-content">
        {activeTab === 'general' ? (
          <DocumentsSection
            title=" Documents Gnraux"
            description="Documents accessibles  tous les employs"
            documents={generalDocuments}
            onDownload={handleDownload}
            getCategoryIcon={getCategoryIcon}
            getCategoryLabel={getCategoryLabel}
            formatFileSize={formatFileSize}
            formatDate={formatDate}
            emptyMessage="Aucun document gnral disponible"
          />
        ) : (
          <DocumentsSection
            title=" Mes Documents Personnels"
            description="Vos documents personnels (fiches de paie, contrats, etc.)"
            documents={personalDocuments}
            onDownload={handleDownload}
            getCategoryIcon={getCategoryIcon}
            getCategoryLabel={getCategoryLabel}
            formatFileSize={formatFileSize}
            formatDate={formatDate}
            emptyMessage="Aucun document personnel disponible"
            isPersonal={true}
          />
        )}
      </div>

      <div className="dashboard-footer">
        <div className="info-box">
          <h3> Informations importantes</h3>
          <ul>
            <li> Les <strong>documents gnraux</strong> sont accessibles  tous les employs</li>
            <li> Les <strong>documents personnels</strong> sont spcifiques  votre compte</li>
            <li> Les documents personnels sont automatiquement supprims aprs 1 mois</li>
            <li> Cliquez sur "Tlcharger" pour sauvegarder un document sur votre appareil</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default EmployeeDashboard;
